/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .bgLastraMain {
    background-image: url("./images/gradient.jpg");
    background-size: cover;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
  }

  .mainLastraContent {
    /* background-color: rgba(0, 0, 0, 0.1); */
    max-width: 1200px;
    width: 95%;
    height: 100%;
    display: flex;
    flex-flow: column;
    margin-top: 50px;
  }

  .mainLastraContentHeader{
    width: 100%;
    display: flex;
    flex-flow: row;
    justify-content: space-between;
  }

  .mainLastraContentHeader p {
    font-size: 22px;
    font-family: Nunito, sans-serif;
    font-weight: 900;
    color: #CED4DA;
    margin-left: 10px;
  }

  .mainLastraContentHeader > div > header {
    font-size: 55px;
    font-family: Nunito, sans-serif;
    font-weight: 900;
    color: #CED4DA;
    height: fit-content;
  }

  .mainLastraContentHeaderRightSection{
    display: flex;
    flex-flow: column;
    justify-content: flex-end;
  }

  .mainLastraContentHeaderRightSection h1 {
    font-size: 16px;
    font-family: Nunito, sans-serif;
    font-weight: 900;
    color: #CED4DA;
    margin-bottom: 25px;
  }

  .mainLastraContentHeaderRightSection span {
    padding: 0;
    margin-bottom: 20px;
  }

  .mainLastraContentNameBar {
    width: 100%;
    font-size: 8px;
    font-family: Nunito, sans-serif;
    font-weight: 900;
    color: #CED4DA;
    text-align: center;
    display: grid;
    grid-row: auto;
    grid-template-columns: 25% 15% 15% 15% 15% 15%;
  }

  .mainLastraContentServerList {
    width: 100%;
    display: grid;
    padding-top: 5px;
    gap: 3px;
  }

  .mainLastraFooter {
    width: 100%;
    height: 250px;
    margin-top: 10px;
    display: grid;
    grid-template-columns: 33% auto 33%;
    place-items: center;
  }

  .mainLastraFooter > img {
    height: 100%;
    position: relative;
    top: -100px;
  }
}

